.popup__submit-button {
  background-color: #000;
  color: #fff;
  border: none;
  height: 50px;
  margin: 0;
  margin-top: 48px;
  margin-bottom: 37px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  cursor: pointer;
  border-radius: 2px;
  
}


.popup__submit-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 899px) {

  .popup__submit-button {
    height: 46px;
    margin-top: 45px;
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 17px;
  }
}