.header__link-list {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #a9a9a9;
  text-decoration: none;
  transition: opacity 0.1s ease-in;
  padding: 0;
  margin: 0;
}

.header__link-list:hover {
  opacity: 0.6;
}



@media screen and (max-width: 899px) {
  .header__link-list {
    margin-right: 0;
  }
}
